.latest-video-container-fluid {
    background-color: #282828;
    background-color: white;
    min-height: 100vh;
    height: auto;

    @media screen and (max-width: 576px) {
        min-height: 480px;
    }

    @media screen and (min-width:576px) and (max-width: 768px) {
        min-height: 450px;
    }

    @media screen and (min-width:768px) and (max-width: 992px) {
        min-height: 625px;
    }

    @media screen and (min-width:992px) and (max-width: 1200px) {
        min-height: 725px;
    }

    @media screen and (min-width:1200px) {
        min-height: 800px;
        margin-top: -70px;
        margin-bottom: 70px;
    }



    .latest-video-container {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        min-height: 100vh;

        @media screen and (max-width: 576px) {
            min-height: 480px;
        }

        @media screen and (min-width:576px) and (max-width: 768px) {
            min-height: 450px;
        }

        @media screen and (min-width:768px) and (max-width: 992px) {
            min-height: 625px;
        }

        @media screen and (min-width:992px) and (max-width: 1200px) {
            min-height: 725px;
        }

        @media screen and (min-width:1200px) {
            min-height: 800px;
        }


        .header-container {
            width: 100%;
            display: flex;
            height: 100px;
            align-items: center;
            justify-content: center;

            .header {
                text-align: center;
                font-weight: bold;
                font-size: 25px;
                font-family: sans-serif;
            }
        }

        h4.title {
            font-family: sans-serif;
            font-weight: bold;
            font-size: 35px;
            margin: 0px 0px 30px 0px;
            letter-spacing: -2px;
            color: black;

            @media screen and (min-width: 1200px) {
                margin: 0px 0px 50px 0px;
            }
        }

        iframe.latest-video {
            width: 100%;
            height: 730px;
            background-color: white;
            border: 3px solid white;

            @media screen and (max-width: 576px) {
                width: calc(100vw - 30px);
                height: calc((100vw - 30px) * (9 / 16)) !important;
            }

            @media screen and (min-width:576px) and (max-width: 768px) {
                height: 290px;
            }

            @media screen and (min-width:768px) and (max-width: 992px) {
                height: 390px;
            }

            @media screen and (min-width:992px) and (max-width: 1200px) {
                height: 525px;
            }



        }
    }
}